.loader-pulse {
  display: inline-block;
  position: relative;
  width: 29px;
  height: 29px;
}

.loader-pulse div {
  position: absolute;
  border: 2px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: pulse 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.loader-pulse div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes pulse {
  0% {
    top: 14px;
    left: 14px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 29px;
    height: 29px;
    opacity: 0;
  }
}

